<template>
  <div class="gallery-edit">
    <div class="header">
      <div>Imagen</div>
      <div>Descripción</div>
      <div>Acciones</div>
    </div>
    <div class="body">
      <inmueble-galeria-editar-item v-for="(item, index) in images" :item="item" @changed="data => changedItem(index, data)" @unchanged="changedItem(index)" :key="index"></inmueble-galeria-editar-item>
    </div>
    <input type="file" ref="fileInput" @change="getFile" accept=".jpg,.jpeg,.png" />
  </div>
</template>

<script>
import { ref, computed, watch } from "vue";
import { useStore } from "vuex";
import axios from "axios";

import InmuebleGaleriaEditarItem from "@/components/inmuebles/InmuebleGaleriaEditarItem.vue"

export default {
  setup() {
    const 
      store = useStore(),
      images = computed(() => store.getters["property/gallery"]),
      fileInput = ref(null),
      imageToUpload = ref(null),
      getFile = () => {
        if (fileInput.value.files.length > 0) {
          if (fileInput.value.files[0]["type"].includes("image")) {
            imageToUpload.value = fileInput.value.files[0];
          }
        }
      },
      changedItem = ((index, data) => {
        const item = {
          index,
          change: null
        };
        if(data) {
          item.change = data;
        }

        store.commit("property/changeGalleryItem", item);
      });

    watch(imageToUpload, (image) => {
      const formData = new FormData();
      formData.append("file", image);
      axios.post("upload", formData, { headers: { "Content-Type": "multipart/form-data" } })
        .then((response) => {
          const 
            imageUploaded = response.data.image,
            imageName = response.data.name;
          if(imageUploaded) {
            store.commit("property/setGalleryItem", {
              active: "1",
              type: "gallery",
              image: imageUploaded,
              name: imageName,
              description: null,
              order: images.value.length + 1
            });
          }
        })
        .catch((error) => {
          console.log(error);
        });
    });

    return {
      images,
      fileInput,
      getFile,
      changedItem
    };
  },
  components: {
    InmuebleGaleriaEditarItem
  }
};
</script>

<style lang="scss">
.gallery-edit {
  min-width: 800px;
  .header div,
  .body .edit-row > div {
    &:nth-child(1) {
      width: 19%;
    }
    &:nth-child(2) {
      width: 67%;
      padding: 0 0.5rem;
    }
    &:nth-child(3) {
      width: 14%;
    }
  }
  .header {
    display: flex;
    border-bottom: 1px solid gainsboro;
  }
  .body {
    max-height: 465px;
    margin-bottom: 1rem;
    border-bottom: 1px solid gainsboro;
    overflow-y: auto;
    .edit-row {
      display: flex;
      align-items: center;
      padding: 0.5rem 0.25rem;
      border-bottom: 1px solid gainsboro;
      &:last-child {
        border-bottom: none;
      }
      &.deleted {
        opacity: 0.3;
      }
      div {
        &:nth-child(1),
        &:nth-child(3) {
          text-align: center;
        }
        img {
          max-width: 133px;
          max-height: 100px;
        }
        .button-action {
          margin: 0 1rem 0 0;
          padding: 0;
          background: transparent;
          border: none;
          &:disabled {
            cursor: not-allowed;
          }
          &:last-child {
            margin-right: 0;
          }
          &.active {
            i {
              opacity: 1;
            }
          }
          i {
            color: #232f3b;
            opacity: 0.25;
            &:hover {
              opacity: 1;
            }
          }
        }
      }
    }
  }
}
</style>