<template>
  <div class="map-content">
    <div ref="mapWrapper" class="map-wrapper" v-show="mapIsVisible"></div>
    <div class="map-overlay" v-show="!mapIsVisible">
      <img
        src="http://futurplace.local/assets/img/ui/marker.svg"
        class="marker"
      />
    </div>
  </div>
</template>

<script>
import { ref, computed, watch, onMounted } from "vue";
import { useStore } from "vuex";
import { Loader } from "@googlemaps/js-api-loader";
import html2canvas from "html2canvas";
import axios from "axios";

import { useModelWrapper } from "@/composables/utils";

const GOOGLE_MAPS_API_KEY = "AIzaSyCRrSICYKpc3x-Ukf4OOFZvymaQSXodhEQ";

export default {
  props: {
    address: [String, Boolean],
    latitude: String,
    longitude: String,
    zoom: Number,
    map_image: String
  },
  setup(props, { emit }) {
    const 
      store = useStore(),
      loader = new Loader({ apiKey: GOOGLE_MAPS_API_KEY }),
      lat = useModelWrapper(props, emit, "latitude"),
      lng = useModelWrapper(props, emit, "longitude"),
      zoomMap = useModelWrapper(props, emit, "zoom"),
      imageMap = useModelWrapper(props, emit, "map_image"),
      map = ref(null),
      geocoder = ref(null),
      marker = ref(null),
      location = ref(null),
      mapWrapper = ref(null),
      mapIsVisible = ref(false),
      addressSearch = ref(false),
      changeMapImage = computed(() => store.getters["property/changeMapImage"]),
      getGeocodign = () => {
        if (!geocoder.value) {
          /* eslint-disable */
          geocoder.value = new google.maps.Geocoder();
          /* eslint-enable */
        }

        geocoder.value.geocode(
          { address: props.address },
          (results, status) => {
            console.log("Buscando coordenadas");
            if (status === "OK") {
              location.value = results[0].geometry.location;
              mapShow();
            } else {
              console.log(
                "Geocode was not successful for the following reason: " + status
              );
            }
          }
        );
      },
      mapShow = () => {
        if (!map.value) {
          /* eslint-disable */
          map.value = new google.maps.Map(mapWrapper.value, {
            center: location.value,
            zoom: zoomMap.value,
            mapTypeControl: false,
            scaleControl: false,
            streetViewControl: false,
            rotateControl: false,
            fullscreenControl: false,
          });

          marker.value = new google.maps.Marker({
            map: map.value,
            position: location.value,
            draggable: true,
            icon: require("@/assets/img/ui/marker.png"),
          });
          /* eslint-enable */

          marker.value.addListener("dragend", (evn) => {
            location.value = evn.latLng;
            map.value.setCenter(location.value);
          });
          
          map.value.addListener("zoom_changed", () => {
            zoomMap.value = map.value.getZoom();
          });
        } else {
          map.value.setCenter(location.value);
          map.value.setZoom(zoomMap.value);
          marker.value.setPosition(location.value);
        }

        mapIsVisible.value = true;
        addressSearch.value = true;
      };

    watch(
      () => props.address,
      (address) => {
        if (address && addressSearch.value) {
          getGeocodign();
        }
      }
    );

    watch(location, (location) => {
      if (location) {
        lat.value = location.lat().toFixed(7);
        lng.value = location.lng().toFixed(7);
      }
    });

    watch(changeMapImage, () => {
      map.value.setOptions({ zoomControl: false });

      setTimeout(() => {
        html2canvas(mapWrapper.value, {
            useCORS: true,
            imageTimeout: 0,
            y: window.scrollY + mapWrapper.value.getBoundingClientRect().top
          }).then((canvas) => {

            canvas.toBlob((blob) => {
              const formData = new FormData();
              formData.append("file", blob, "image.jpg");

              axios.post("upload", formData)
                .then((response) => {
                  const 
                    imageUploaded = response.data.image;
                  if(imageUploaded) {
                    imageMap.value = imageUploaded;
                  }
                })
                .catch((error) => {
                  console.log(error);
                })
            }, "image/jpeg", 0.9);
          });
      }, 1000);
    });

    onMounted(async () => {
      await loader.load();

      if (props.latitude && props.longitude) {
        /* eslint-disable */
        location.value = new google.maps.LatLng(
          parseFloat(props.latitude),
          parseFloat(props.longitude)
        );
        /* eslint-enable */
        mapShow();
      } else {
        addressSearch.value = true;
      }
    });

    return {
      mapWrapper,
      mapIsVisible
    };
  },
};
</script>

