<template>
  <div class="edit-row" :class="{ 'deleted': item.delete }">
    <div>
      <img :src="item.image">
    </div>
    <div>
      <div class="input-wrapper with-icon primary mw-100p only-bottom">
        <input type="text" v-model.lazy="description">
        <span class="input-icon">¶</span>
      </div>
    </div>
    <div>
      <template v-if="item.id">
        <button class="button-action" :class="{ 'active': is_cover == '1' }" :title="is_cover == '1' ? 'Portada actual' : 'Mostrar en portada'" @click="is_cover = '1'" :disabled="item.delete">
          <icon icon="imagen"></icon>
        </button>
        <button class="button-action" :title="active == '1' ? 'Desactivar' : 'Activar'" @click="active = active == '1' ? '0' : '1'" :disabled="item.delete">
          <icon icon="mostrar" v-show="active == '1'"></icon>
          <icon icon="ocultar" v-show="active == '0'"></icon>
        </button>
        <button class="button-action" :title="item.delete ? 'Deshacer' : 'Eliminar'" @click="deleteImage">X</button>
      </template>
    </div>
  </div>
</template>

<script>
import { ref, reactive, toRefs, watch, onMounted } from "vue";

import Icon from "@/core/components/Icon.vue";
export default {
  props: {
    item: Object
  },
  setup(props, { emit }) {
    const 
      image = reactive({
        description: null,
        active: null,
        is_cover: null
      }),
      firstEmit = ref(true),
      deleteImage = () => {
        if(!Object.prototype.hasOwnProperty.call(image, 'delete')) {
          image.delete = true;
        } else {
          delete image.delete;
        }
      };

    watch(() => props.item.is_cover, (is_cover) => {
      image.is_cover = is_cover;
    });

    watch(image, () => {
      const 
        change = [];

      if(image.description != null) {
        image.description = image.description.trim() == "" ? null : image.description.trim();
      }

      for(let key in image) {
        // console.log(`image[${ key }]: ${ image[key] } & item[${ key }]: ${ props.item[key] }`);
        // console.log(image[key] != props.item[key]);
        if(image[key] != props.item[key]) {
          change[key] = image[key];
        }
      }

      if(Object.keys(change).length > 0) {
        emit("changed", change);
        firstEmit.value = false;
      } else if(!firstEmit.value) {
        emit("unchanged");
      }
    });

    onMounted(() => {
      image.description = props.item.description;
      image.active = props.item.active;
      image.is_cover = props.item.is_cover;
    });

    return {
      ...toRefs(image),
      deleteImage
    };
  },
  components: {
    Icon
  }
};
</script>

