<template>
  <li>

    <icon v-if="catalog.icon" :icon="catalog.icon"></icon>
    <span>{{ catalog.catalog }}</span>

    <template v-if="catalog.type == 'Número'">
      <div class="input-wrapper with-icon inline primary mw-70 only-bottom">
        <input type="number" min="1" v-model.lazy="value">
        <span class="input-icon">#</span>
      </div>
    </template>

    <template v-else-if="catalog.type == 'Binario'">
      <div class="custom-radio">
        <input type="radio" value="1" :id="`radio-si-${ catalog.id }`" v-model="value">
        <label :for="`radio-si-${ catalog.id }`">Si</label>
      </div>
      <div class="custom-radio">
        <input type="radio" value="0" :id="`radio-no-${ catalog.id }`" v-model="value">
        <label :for="`radio-no-${ catalog.id }`">No</label>
      </div>
      <template v-if="property_data_id">
        <button class="button-action" @click="value = value != '' ? '' : 1" :title="value != '' ? 'Ocultar' : 'Mostrar'">
          <icon icon="mostrar" v-show="value != ''"></icon>
          <icon icon="ocultar" v-show="value == ''"></icon>
        </button>
      </template>
    </template>

    <template v-else-if="catalog.type == 'Texto'">
      <div class="input-wrapper with-icon inline primary mw-125 only-bottom">
        <input type="text" v-model="option" @focus="showOptions = true" @blur="inputOptionsBlured">
        <span class="input-icon">¶</span>
        <ul class="input-options" v-show="showOptions && filteredOptions" tabindex="0">
          <li 
            v-for="filteredOption in filteredOptions" 
            @click="setOption(filteredOption)"
            :key="filteredOption.id"
          >{{ filteredOption.option }}</li>
        </ul>
      </div>
    </template>

    <span v-if="catalog.unit" v-html="catalog.unit"></span>

  </li>
</template>

<script>
import { ref, reactive, toRefs, computed, watch, onMounted } from "vue";
import { useStore } from 'vuex';

import Icon from "@/core/components/Icon.vue";
export default {
  props: {
    catalog: Object
  },
  setup(props) {
    const 
      store = useStore(),
      data = reactive({
        id: null,
        value: null,
        is_id: null,
        is_value: null,
        property_data_id: 0,
      }),
      option = ref(null),
      options = ref(null),
      showOptions = ref(true),
      setOption = (currentOption => {
        option.value = currentOption.option;
        data.value = currentOption.id;
        data.is_new_option = 0;
        showOptions.value = false;
      }),
      filteredOptions = computed(() => {
        if(options.value && option.value != "" && option.value != null) {
          const filtered = options.value.filter( filteredOption => filteredOption.option.toLowerCase().includes( option.value.trim().toLowerCase() ) );
          if(filtered.length > 0) {
            return filtered;
          }
        }
        return false;
      }),
      inputOptionsBlured = (evn => {
        let isNotOptions = false;
        if(evn.relatedTarget) {
          if(evn.relatedTarget.className != "input-options") {
            showOptions.value = false;
            isNotOptions = true;
          }
        } else {
          showOptions.value = false;
          isNotOptions = true;
        }

        if(isNotOptions && option.value != null) {
          if(option.value.trim() != "" && option.value != data.value) {
            let isNewOption = true;

            if(options.value) {
              const filteredOption = options.value.filter( currentOption => currentOption.option.toLowerCase() == option.value.trim().toLowerCase() );
              if(filteredOption.length > 0) {
                data.value = filteredOption[0].id;
                data.is_new_option = 0;
                isNewOption = false;
              }
            }

            if(isNewOption) {
                data.value = option.value.trim();
                data.is_new_option = 1;
            }
            
          } else if(option.value.trim() == "") {
            data.value = "";
          }
        }
      }),
      inStore = ref(false);

    watch(() => data.value, (value) => {
      if(props.catalog.property_data_id) {
        if(value != props.catalog.value) {
          if(data.value == "") {
            data.delete = true;
          } else {
            if("delete" in data) {
              // console.log("borrar delete");
              delete data.delete;
            }
          }
          // console.log("editar");
          store.commit("property/setChange", { type: "information", change: { ...data } });
          inStore.value = true;
        } else {
          if(inStore.value) {
            store.commit("property/deleteChange", { type: "information", change: { id: data.id } });
            inStore.value = false;
          }
        }
      } else {
        if(value.trim() != "") {
          // console.log("nuevo");
          store.commit("property/setChange", { type: "information", change: { ...data } });
          inStore.value = true;
        } else {
          if(inStore.value) {
            store.commit("property/deleteChange", { type: "information", change: { id: data.id } });
            inStore.value = false;
          }
        }
      }
    });

    onMounted(() => {
      data.id = props.catalog.id;
      data.is_id = props.catalog.is_id != "0";
      data.is_value = props.catalog.is_value != "0";
      data.property_data_id = props.catalog.property_data_id ? props.catalog.property_data_id : 0;
      if(!props.catalog.options) {
        data.value = props.catalog.value;
      } else {
        options.value = props.catalog.options;
        data.is_new_option = 0;
        if(props.catalog.value != null) {
          option.value = props.catalog.options.filter( option => option.id == props.catalog.value)[0].option;
          showOptions.value = false;
        }
      }

      /*if(!props.catalog.property_data_id) {
        data.delete = null;
      }*/
    });

    return {
      ...toRefs(data),
      option,
      setOption,
      showOptions,
      filteredOptions,
      inputOptionsBlured
    }
  },
  components: {
    Icon
  }
}
</script>

